}

.last-game__link:hover .last-game__title {
  color: var(--color-purple);
  transition: color 0.3s ease-in-out;
}

.last-game__body {
  display: flex;
}

.last-game__bg {
  display: flex;
  background-image: url(./../img/timeline.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.last-game__video {
  width: 100%;
  height: 100%;
}

.last-game__text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-left: 20px;
  text-align: left;
}

.last-game__title {
  margin-bottom: 20px;
  font-family: "Space Mono";
  font-weight: 700;
  font-style: italic;
  font-size: 32px;
  text-transform: uppercase;
  color: var(--color-white);
  transition: color 0.3s ease-in-out;
}

.last-game__dsr {
  width: 335px;
  font-family: "Roboto Mono";
  font-weight: 400;
  font-size: 20px;
  line-height: 30px;
  color: var(--color-white);
}

.game-popup {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 999;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 8, 64, 0.5);
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.5s, visibility 0.5s;
}

.game-popup__body {
  position: relative;
  bottom: -100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 30px;
  padding-top: 120px;
  width: 920px;
  height: 460px;
  background-color: var(--color-white);
  transition: bottom 0.5s;
}

.game-popup__close {
  border: none;
  padding: 0;
  background-color: transparent;
  position: absolute;
  right: 50px;
  top: 30px;
  width: 40px;
  height: 40px;
}

.game-popup__close span {
  position: absolute;
  left: 0;
  top: 50%;
  display: block;
  width: 60px;
  height: 2px;
  background-color: #b5b5b5;
  transform: translateY(-50%);
}

.game-popup__close span:first-child {
  transform: rotate(45deg);
}

.game-popup__close span:last-child {
  transform: rotate(-45deg);
}

.game-popup__title {
  margin-bottom: 30px;
  font-family: "Space Mono Normal";
  font-weight: 700;
  font-size: 60px;
  text-transform: uppercase;
}

.game-popup__links {
  display: flex;
}

.popup-open {
  opacity: 1;
  visibility: visible;
  transition: opacity 0.5s, visibility 0.5s;
}

.body-popup-open {
  bottom: 0;
  transition: bottom 0.5s;
}

.android-link {
  margin-right: 20px;
  width: 356px;
  height: 105px;
