  justify-content: space-between;
}

.media__item:not(:last-child) {
  margin-right: 40px;
}

.media__img {
  fill: var(--color-purple);
  transition: fill 0.3s ease-in-out;
}

.media__img:hover {
  fill: var(--color-white);
  transition: fill 0.3s ease-in-out;
}

.site-creator {
  padding-left: 60px;
