    line-height: 18px;
    hyphens: none;
  }
  .divinest-light__text:not(:last-child) {
    margin-bottom: 27px;
  }
  .mobile-projects {
    margin-bottom: 60px;
  }
  .game__text {
    padding-top: 10px;
  }
  .last-game__title {
    margin-top: 10px;
  }
  .game-popup__body {
    border-radius: 0;
    padding-top: 120px;
    width: 100%;
    height: 250px;
  }
  .game-popup__close {
    position: absolute;
    right: 20px;
    top: 25px;
    width: 40px;
    height: 40px;
  }
  .game-popup__close span {
    width: 40px;
    height: 2px;
  }
  .game-popup__title {
